'use client';

import { useState, useEffect } from 'react';
import { Timestamp, collection, addDoc, getDocs, query, where, doc, getDoc } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';

export function useBookingForm(companyId: string, userId: string) {
  const [formData, setFormData] = useState<Record<string, any>>({});
  const [packages, setPackages] = useState<SubscriptionPackage[]>([]);
  const [allBikes, setAllBikes] = useState<(Ebike & { modelName?: string })[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const packageSnap = await getDocs(query(collection(db, 'subscriptionPackages'), where('companyId', '==', companyId)));
        const loadedPackages = packageSnap.docs.map(doc => ({ id: doc.id, ...doc.data() })) as SubscriptionPackage[];
        setPackages(loadedPackages);

        const bikeSnap = await getDocs(query(collection(db, 'ebikes'), where('companyId', '==', companyId)));
        const bikesRaw = bikeSnap.docs.map(doc => ({ id: doc.id, ...doc.data() })) as Ebike[];
        const availableBikes = bikesRaw.filter(bike => bike.status === 'Available');

        const bikesWithModelName = await Promise.all(
          availableBikes.map(async (bike) => {
            let modelName = '';
            if (bike.modelId) {
              try {
                const modelDoc = await getDoc(doc(db, 'ebikeModels', bike.modelId));
                if (modelDoc.exists()) {
                  const modelData = modelDoc.data() as EbikeModel;
                  modelName = modelData.name;
                }
              } catch (error) {
                console.error('‚ùå Error loading model for bike:', bike.id, error);
              }
            }
            return { ...bike, modelName };
          })
        );

        setAllBikes(bikesWithModelName);
      } catch (error) {
        console.error('‚ùå Error loading data:', error);
      } finally {
        setLoading(false);
      }
    };

    if (companyId) fetchData();
  }, [companyId]);

  const handleChange = (key: string, value: any) => {
    setFormData(prev => {
      const updated = { ...prev, [key]: value };

      if (key.startsWith('package')) {
        const selectedPackage = packages.find(pkg => pkg.id === value);
        if (selectedPackage) {
          updated['basePrice'] = selectedPackage.basePrice;
          updated['kmLimit'] = selectedPackage.kmLimit ?? 'Unlimited';
          updated['overageRate'] = selectedPackage.overageRate ?? '-';
          updated['chargingMethod'] = selectedPackage.chargingMethod;
        }
      }

      if (key === 'vehicleSearch') {
        const selectedBike = allBikes.find(bike => bike.vehicleID === value);
        if (selectedBike) {
          updated['stationId'] = selectedBike.stationId || '';
          updated['vin'] = selectedBike.vehicleID || '';
          updated['vehicleModel'] = selectedBike.modelName || '';
          updated['vehicleColor'] = selectedBike.color || '';
          updated['licensePlate'] = selectedBike.plateNumber || '';
        }
      }

      const rentalDays = Number(updated['rentalDays']) || 0;
      const basePrice = Number(updated['basePrice']) || 0;
      const batteryFee = Number(updated['batteryFee']) || 0;
      const deposit = Number(updated['deposit']) || 0;

      if (rentalDays && (basePrice || batteryFee)) {
        const totalAmount = rentalDays * (basePrice + batteryFee);
        updated['totalAmount'] = totalAmount;
        updated['remainingBalance'] = totalAmount - deposit;
      }

      if (key === 'deposit' && updated['totalAmount']) {
        updated['remainingBalance'] = updated['totalAmount'] - deposit;
      }

      if (
        ['rentalStartDate', 'rentalStartHour', 'rentalDays'].includes(key) ||
        (updated['rentalStartDate'] && updated['rentalStartHour'] && updated['rentalDays'])
      ) {
        const startDate = updated['rentalStartDate'];
        const startHour = updated['rentalStartHour'];
        const days = Number(updated['rentalDays']) || 0;

        if (startDate && startHour && days) {
          const startDateTime = new Date(`${startDate}T${startHour}:00`);
          startDateTime.setDate(startDateTime.getDate() + days);

          updated['rentalEndDate'] = startDateTime; // d√πng Date object
        }
      }

      return updated;
    });
  };

  const handleSubmit = async (): Promise<'success' | 'error' | 'validation_error'> => {
    try {
      const {
        rentalStartDate,
        rentalStartHour,
        rentalDays,
        fullName,
        phone,
        idNumber,
        address,
        vehicleModel,
        vehicleColor,
        vin,
        licensePlate,
        batteryCode1,
        batteryCode2,
        batteryCode3,
        batteryCode4,
        package: packageId,
        basePrice,
        batteryFee,
        totalAmount,
        deposit,
        deliveryMethod,
        deliveryAddress,
        helmet,
        charger,
        phoneHolder,
        rearRack,
        raincoat,
        note,
        stationId,
        rentalEndDate,
      } = formData;

      if (!rentalStartDate || !rentalStartHour || !rentalDays) {
        return 'validation_error';
      }

      const startDate = new Date(`${rentalStartDate}T${rentalStartHour}:00`);
      const endDate = rentalEndDate instanceof Date
        ? rentalEndDate
        : new Date(startDate.getTime() + Number(rentalDays) * 86400000);

      await addDoc(collection(db, 'bookings'), {
        companyId: companyId || '',
        stationId: stationId || '',
        userId: userId || '',

        fullName: fullName || '',
        phone: phone || '',
        idNumber: idNumber || '',
        address: address || '',

        vehicleModel: vehicleModel || '',
        vehicleColor: vehicleColor || '',
        vin: vin || '',
        licensePlate: licensePlate || '',

        batteryCode1: batteryCode1 || '',
        batteryCode2: batteryCode2 || '',
        batteryCode3: batteryCode3 || '',
        batteryCode4: batteryCode4 || '',

        rentalStartDate: Timestamp.fromDate(startDate),
        rentalStartHour: rentalStartHour || '',
        rentalDays: Number(rentalDays) || 1,
        rentalEndDate: Timestamp.fromDate(endDate),

        package: packageId || '',
        basePrice: Number(basePrice) || 0,
        batteryFee: Number(batteryFee) || 0,
        totalAmount: Number(totalAmount) || 0,
        deposit: Number(deposit) || 0,
        remainingBalance: (Number(totalAmount) || 0) - (Number(deposit) || 0),

        deliveryMethod: deliveryMethod || 'Pickup at Shop',
        deliveryAddress: deliveryAddress || '',

        helmet: helmet ?? false,
        charger: charger ?? false,
        phoneHolder: phoneHolder ?? false,
        rearRack: rearRack ?? false,
        raincoat: raincoat ?? false,

        note: note || '',
        bookingStatus: 'draft',
        createdAt: Timestamp.now(),
      });

      setFormData({});
      return 'success';
    } catch (error) {
      console.error('‚ùå Submit error:', error);
      return 'error';
    }
  };

  return {
    formData,
    setFormData,
    handleChange,
    handleSubmit,
    packages,
    setPackages,
    loading,
    setLoading,
    allBikes,
    setAllBikes,
    resetForm: () => setFormData({}), // ‚úÖ th√™m d√≤ng n√†y
  };  
}
