'use client';

import { useEffect } from 'react';
import { useBookingForm } from '@/src/hooks/useBookingForm';
import { useUser } from '@/src/context/AuthContext';
import { Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { format } from 'date-fns';
import { formatCurrency } from '@/src/utils/formatCurrency';
import { parseCurrencyString } from '@/src/utils/parseCurrencyString';
import { sanitizeFirestoreData } from '@/src/utils/sanitizeFirestoreData'; // ‚úÖ Th√™m d√≤ng n√†y
import ProgramSelector from './ProgramSelector';

interface Props {
  editingBooking: Record<string, any> | null;
  stationNames: Record<string, string>;
  companyNames: Record<string, string>;
  userNames: Record<string, string>;
  packageNames: Record<string, string>;
  packages: SubscriptionPackage[];
  ebikes: Ebike[];
  onSave: (data: Record<string, any>) => void;
  onCancel: () => void;
}

const formatDateInput = (date: any) => {
  if (!date) return '';
  let realDate: Date | null = null;
  if (date instanceof Date) realDate = date;
  else if (typeof date?.toDate === 'function') realDate = date.toDate();
  else if (typeof date === 'string' || typeof date === 'number') realDate = new Date(date);
  if (!realDate || isNaN(realDate.getTime())) return '';
  return format(realDate, 'yyyy-MM-dd');
};

export default function BookingForm({
  editingBooking,
  stationNames,
  companyNames,
  userNames,
  packageNames,
  packages,
  ebikes,
  onSave,
  onCancel,
}: Props) {
  const { companyId, user } = useUser();
  const {
    formData: form,
    handleChange,
    resetForm,
    setFormData,
  } = useBookingForm(companyId ?? '', user?.uid ?? '');

  useEffect(() => {
    if (editingBooking) {
      const { id, createdAt, updatedAt, ...rest } = editingBooking;
      setFormData(rest);
    } else {
      resetForm();
    }
  }, [editingBooking]);

  const handleSubmit = () => {
    if (!form.fullName || !form.phone || !form.vehicleModel) {
      alert('Please fill Full Name, Phone, and Vehicle Model.');
      return;
    }

    // ‚úÖ ƒê√ÇY L√Ä FIX QUAN TR·ªåNG ‚Üí sanitize d·ªØ li·ªáu tr∆∞·ªõc khi g·ª≠i onSave
    onSave(sanitizeFirestoreData(form));
  };

  const handleCurrencyChange = (key: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
    handleChange(key, parseCurrencyString(e.target.value));
  };

  return (
<div className="w-full max-w-7xl mx-auto p-6 bg-white rounded-lg shadow space-y-10 overflow-y-auto max-h-[90vh]">
      <Section title="Customer Information">
        <TwoColumns>
          <Input placeholder="Full Name" value={form.fullName || ''} onChange={(e) => handleChange('fullName', e.target.value)} />
          <Input placeholder="Phone" value={form.phone || ''} onChange={(e) => handleChange('phone', e.target.value)} />
          <Input placeholder="ID Number" value={form.idNumber || ''} onChange={(e) => handleChange('idNumber', e.target.value)} />
          <Input placeholder="Address" value={form.address || ''} onChange={(e) => handleChange('address', e.target.value)} />
        </TwoColumns>
      </Section>

      <Section title="Vehicle Information">
        <TwoColumns>
          <Input placeholder="Vehicle Model" value={form.vehicleModel || ''} onChange={(e) => handleChange('vehicleModel', e.target.value)} />
          <Input placeholder="Vehicle Color" value={form.vehicleColor || ''} onChange={(e) => handleChange('vehicleColor', e.target.value)} />
          <Input placeholder="VIN" value={form.vin || ''} onChange={(e) => handleChange('vin', e.target.value)} />
          <Input placeholder="License Plate" value={form.licensePlate || ''} onChange={(e) => handleChange('licensePlate', e.target.value)} />
        </TwoColumns>
      </Section>

      <Section title="Rental Period">
        <TwoColumns>
          <Input type="date" value={formatDateInput(form.rentalStartDate)} onChange={(e) => handleChange('rentalStartDate', e.target.value)} />
          <Input placeholder="Rental Start Hour" value={form.rentalStartHour || ''} onChange={(e) => handleChange('rentalStartHour', e.target.value)} />
          <Input type="number" placeholder="Rental Days" value={form.rentalDays || 1} onChange={(e) => handleChange('rentalDays', Number(e.target.value))} />
          <Input type="date" value={formatDateInput(form.rentalEndDate)} onChange={(e) => handleChange('rentalEndDate', e.target.value)} />
        </TwoColumns>
      </Section>

      <Section title="Pricing & Deposit">
        <TwoColumns>
          <select className="border p-2 rounded" value={form.package || ''} onChange={(e) => handleChange('package', e.target.value)}>
            <option value="">Select Package</option>
            {Object.entries(packageNames).map(([id, name]) => (
              <option key={id} value={id}>{name}</option>
            ))}
          </select>
          <Input placeholder="Base Price" value={formatCurrency(form.basePrice)} onChange={handleCurrencyChange('basePrice')} />
          <Input placeholder="Battery Fee" value={formatCurrency(form.batteryFee || 0)} onChange={handleCurrencyChange('batteryFee')} />
          <Input placeholder="Total Amount" value={formatCurrency(form.totalAmount)} onChange={handleCurrencyChange('totalAmount')} />
          <Input placeholder="Deposit" value={formatCurrency(form.deposit)} onChange={handleCurrencyChange('deposit')} />
          <Input placeholder="Remaining Balance" value={formatCurrency(form.remainingBalance)} onChange={handleCurrencyChange('remainingBalance')} />
        </TwoColumns>
      </Section>

      <Section title="Programs">
        <TwoColumns>
          <ProgramSelector companyId={companyId ?? ''} selectedProgramId={form.programId || null} onChange={(value) => handleChange('programId', value)} />
        </TwoColumns>
      </Section>

      <Section title="Accessories">
        <TwoColumns>
          <label><input type="checkbox" checked={form.helmet || false} onChange={(e) => handleChange('helmet', e.target.checked)} /> Helmet</label>
          <label><input type="checkbox" checked={form.charger || false} onChange={(e) => handleChange('charger', e.target.checked)} /> Charger</label>
          <label><input type="checkbox" checked={form.rearRack || false} onChange={(e) => handleChange('rearRack', e.target.checked)} /> Rear Rack</label>
          <label><input type="checkbox" checked={form.phoneHolder || false} onChange={(e) => handleChange('phoneHolder', e.target.checked)} /> Phone Holder</label>
          <label><input type="checkbox" checked={form.raincoat || false} onChange={(e) => handleChange('raincoat', e.target.checked)} /> Raincoat</label>
        </TwoColumns>
      </Section>

      <Section title="Battery Info">
        <TwoColumns>
          <Input placeholder="Battery Code 1" value={form.batteryCode1 || ''} onChange={(e) => handleChange('batteryCode1', e.target.value)} />
          <Input placeholder="Battery Code 2" value={form.batteryCode2 || ''} onChange={(e) => handleChange('batteryCode2', e.target.value)} />
          <Input placeholder="Battery Code 3" value={form.batteryCode3 || ''} onChange={(e) => handleChange('batteryCode3', e.target.value)} />
          <Input placeholder="Battery Code 4" value={form.batteryCode4 || ''} onChange={(e) => handleChange('batteryCode4', e.target.value)} />
        </TwoColumns>
      </Section>

      <Section title="Rental Method">
        <TwoColumns>
          <select className="border p-2 rounded" value={form.deliveryMethod || ''} onChange={(e) => handleChange('deliveryMethod', e.target.value)}>
            <option value="">Select Method</option>
            <option value="Pickup at Shop">Pickup at Shop</option>
            <option value="Deliver to Address">Deliver to Address</option>
          </select>
          {form.deliveryMethod === 'Deliver to Address' && (
            <Textarea placeholder="Delivery Address" value={form.deliveryAddress || ''} onChange={(e) => handleChange('deliveryAddress', e.target.value)} />
          )}
        </TwoColumns>
      </Section>

      <Section title="Station">
        <TwoColumns>
          <select className="border p-2 rounded" value={form.stationId || ''} onChange={(e) => handleChange('stationId', e.target.value)}>
            <option value="">Select Station</option>
            {Object.entries(stationNames).map(([id, name]) => (
              <option key={id} value={id}>{name}</option>
            ))}
          </select>
        </TwoColumns>
      </Section>

      <Section title="Notes">
        <Textarea placeholder="Additional Notes" value={form.note || ''} onChange={(e) => handleChange('note', e.target.value)} />
      </Section>

      <div className="flex justify-end gap-4 pt-4">
        <Button onClick={handleSubmit}>Save</Button>
        <Button variant="outline" onClick={onCancel}>Cancel</Button>
      </div>
    </div>
  );
}

function Section({ title, children }: { title: string; children: React.ReactNode }) {
  return (
    <div className="space-y-4">
      <h3 className="text-xl font-semibold">{title}</h3>
      {children}
    </div>
  );
}

function TwoColumns({ children }: { children: React.ReactNode }) {
  return <div className="grid grid-cols-1 md:grid-cols-2 gap-4">{children}</div>;
}
